










import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import NavBar from '@/components/common/NavBar.vue';
import UserDetail from '@/components/usercomponent/UserDetail.vue';

@Component({
  components: {UserDetail, NavBar}
})
export default class UperInfo extends Vue {
  uperInfo ={}
  userInfo ={}
  model={}
  $http!: { get: (arg0: string) => any }

  async fetchUserInfo() {
    const res = await this.$http.get('/web/api/user/' + localStorage.getItem('id'));
    this.userInfo = res.data[0];
  }

  created() {
    if(localStorage.getItem('id') && localStorage.getItem('objtoken')){
      this.fetchUserInfo();
    }
    this.articleData();
  }

  //当前视屏的数据
  async articleData() {
    const res = await this.$http.get('/web/api/article/'+this.$route.params.id);
    this.uperInfo = res.data[0].userinfo
  }
}
