




















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class Detail extends Vue {
  @Prop() detail: any;

  handlePush(){
    //当前的路由和目标路由不一样才跳转
    if(this.$route.path !== '/article/'+this.detail.id){
      this.$router.push('/article/'+this.detail.id)
    }
  }
}
